@import "../../../sass/abstracts/mixins";
@import "../../../sass/abstracts/variables";
@import "../../../sass/base/layout";
@import "../../../sass/base/typography";
.section {
  background-color: transparent;
  padding: 5rem 0;
}
.row {
  text-align: center;
  padding:0;
}
.profile {
  height: 50vh;
  display: grid;
  grid-template-columns: repeat(20, 5fr);
  grid-template-rows: repeat(20, 5fr);
  &_image {
    grid-row: 1/11;
    grid-column: 2/6;
    border-radius: 1rem;
    .image {
      width: 120%;
      border-radius: 2rem;
    }
  }
  &_description {
    color: $color-primary-light;
    // padding: 2rem;
    // font-size: 85%;
    // background-image: url(../../../Images/Profile/profile_bg.png);
    // background-size: 105%;
    // background-repeat: no-repeat;
    // background-position: center;
    text-align: justify;
    padding-left: 2rem;
    grid-row: 2/21;
    grid-column: 7/21;
    p {
      line-height: 1.5;
    }
  }
  &_name {
    grid-row: 20/21;
    grid-column: 17/20;
    color: $color-primary-light;
  }
}
